:root {
  --gallery-gap: 20px;
}

* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #e6dbda;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

img {
  max-width: 100%;
  height: 210px;
  display: block;
}

.container {
  width: 1300px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
}

.search-form {
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.search-form > input {
  width: 358px;
  height: 50px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(81, 15, 204, .3);
  border-radius: 4px;
  outline: none;
  margin-right: 12px;
  padding: 15px 16px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.search-form > input:focus {
  border: 1px solid rgba(226, 15, 15, .3);
}

button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(81, 15, 204, .3);
  border-radius: 4px;
  font-size: 16px;
}

button:hover {
  background-color: rgba(226, 15, 15, .3);
}

.gallery {
  margin-top: calc(-1 * var(--gallery-gap) );
  margin-left: calc(-1 * var(--gallery-gap) );
  flex-wrap: wrap;
  align-items: stretch;
  display: flex;
}

.gallery-item {
  flex-basis: calc(100% / 4 - var(--gallery-gap) );
  margin-left: var(--gallery-gap);
  margin-top: var(--gallery-gap);
}

.photo-card {
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 3px 8px rgba(0, 0, 0, .24);
}

.info {
  justify-content: space-between;
  padding-left: 5px;
  padding-right: 5px;
  display: flex;
}

.info-item {
  text-align: center;
}

.load-more {
  visibility: hidden;
  height: 50px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px 16px;
  display: flex;
}

.visible {
  visibility: visible;
}

